import headerNav from '@/components/header_nav.vue'
export default {
	data() {
		return {
			title: '救济申请',
			// up: '问题反馈',
			// url_ri:'/pages/center/question',
			icon: '../../static/icon/left_whitch.png',
			list:[],
			current: 0,
			type:1,
			page: 0,
			pages: 1,
			loadshow:true,
			tab: [{
				name: '处理中'
			}, {
				name: '已处理'
			}],
		}
	},
	methods: {
		tabs(index , type) {
			this.current = index;
			this.list = [];
			this.page = 0;
			this.pages = 1;
			this.loadshow = true;
			this.type = type;
			this.Loadthepage();
			console.log("1213")
		},
		
		Loadthepage() {
			var self = this;
			if(!self.loadshow){
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				self.$service.auth(self, self.$service.api.record.reliefFund, "GET", {
					page: page,
					limit: 15,
					type:self.type
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.last_page;
					self.page = page;
					
					var lists = res.data.data;
					lists.forEach((v, i) => {
						self.list.push(v);
					});
					if (self.pages == self.page || self.list.length == 0) {
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},
	},
	components: {
		headerNav
	},
	onShow() {
		this.Loadthepage()
		/* this.$service.auth(this , this.$service.api.record.reliefFund , "GET" , {
			page:1,
			limit:15,
			type:1
		},function(self , res){
			console.log(res);
			self.list = res.data.data;
		}); */
	}
}
